*
{
  margin: 0;
  padding: 0;
  font-family: "verdana", cursive, sans-serif;
  color: black;
}
body{
  background-image: url('https://media.istockphoto.com/id/1478776181/video/flying-pink-neon-question-marks-on-a-black-background-3d-animation-question-mark-looping.jpg?s=640x640&k=20&c=5iaBIXjs7ydswVcvKqjjqRySe6ksY2njjWn4h3SYQFQ=');
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
}
.header
{
  color: rgb(255, 0, 30);
  text-align: center;
  font-size: 40px;
  font-weight: bold;
}
.app
{
  background: linear-gradient(5deg, #3b1dff 0%, #f71aa2 100%);
  width: 95%;
  min-height: 200px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0,0,0,0.75);
  display: flex;
  justify-content: space-evenly;
}
.question-section{
  color: rgb(36, 75, 193);
  font-size: 30px;
  position: relative;
  width: 100%;
}
.question-count{
  margin-bottom: 20px;
}
.question-count span{
  font-size: 50px;
  font-weight: bold;
}
.question-text{
  margin-bottom: 12px;
  color: rgb(255, 255, 0);
  font-weight:400;
}
.answer-section{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
}
.score-section{
  color: rgb(0, 20, 3);
  font-size: 30px;
  font-weight: bold;
  display: flex;
  align-items: center;
  background: linear-gradient(5deg, #3b1dff 0%, #f71aa2 100%);
}
button{
  width: 100%;
  font-size: 16px;
  color: #ffffff;
  background: linear-gradient(#3b1dff 0%, #f71aa2 100%);
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid #234668;
  cursor: pointer;
}
button:hover{
  opacity: 0.8;
}




